<script>
//import ColorTheme from './ColorTheme'
import MyGrid from './MyGrid'
import types from '@/store/MutationType2';
import moment from 'moment'
export default {
  name: 'colors',
  components: { 
    //ColorTheme 
    MyGrid,
  },
  computed:{
    ds(){
      let ds=this.$store.getters[types.NONG_TRAI_GET_DS];
      return ds
    }
  },
  data(){
    return {
      sel:{}
    }
  },
  created(){
    this.$store.dispatch(types.NONG_TRAI_LDS).then(res=>{
      //alert(JSON.stringify(res))
    })
  },
  methods:{
    on_update(){
      alert('update');
    },
    on_do_add(){
      alert('do add');
    },
    on_add(){
      this.sel={
        id:0,
      }
    },
    on_cancel(){
      this.sel={};
    },
    on_edit(x){
      this.sel=Object.assign({},x);

    },
    on_del(x){
      //alert(JSON.stringify(x));
      this.$store.dispatch(types.NONG_TRAI_DEL,x.id)
      .then(res=>{
        //alert(JSON.stringify(res))
      })

    },
    dsView(){
      let kq=[];
      let row;
      for(var x in this.ds){
        row= Object.assign ({},this.ds[x]);
        let ngay_dk=moment(row.ngay_dk).format('DD/MM/YYYY');
        row.ngay_dk=ngay_dk;
        kq.push(row);
      }
      return kq;
      // Object.keys(x).forEach((key,index)=>{
      //   if(key=="ngay_dk"){
      //     let ngay_dk=moment(x[key]).format('DD/MM/YYYY');
      //     kq2[key]=ngay_dk;
      //   }else{
